<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-触发方式1">触发方式</a></li><li><a href="#vue-doc-对齐方向1">对齐方向</a></li><li><a href="#vue-doc-嵌套用法1">嵌套用法</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-dropdown下拉菜单1" class="vue-doc-element">Dropdown 下拉菜单</h1>
<p class="vue-doc-element">展示一组折叠的下拉菜单。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demotrigger></demotrigger>
<demoplacement></demoplacement>
<demonest></demonest>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-dropdownprops1_1" class="vue-doc-element">Dropdown props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>trigger</td>
<td>触发方式，可选值为 hover（悬停）click（点击）custom（自定义），使用 custom 时，需配合 visible 一起使用</td>
<td>String</td>
<td>hover</td>
</tr>
<tr>
<td>visible</td>
<td>手动控制下拉框的显示，在 trigger = &#39;custom&#39; 时使用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>placement</td>
<td>下拉菜单出现的位置，可选值为toptop-starttop-endbottombottom-startbottom-endleftleft-startleft-endrightright-startright-end, 2.12.0 版本开始支持自动识别</td>
<td>String</td>
<td>bottom</td>
</tr>
<tr>
<td>transfer</td>
<td>是否将弹层放置于 body 内，在 Tabs、带有 fixed 的 Table 列内使用时，建议添加此属性，它将不受父级样式影响，从而达到更好的效果</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-dropdownevents1_2" class="vue-doc-element">Dropdown events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-click</td>
<td>点击菜单项时触发</td>
<td>DropdownItem 的 name 值</td>
</tr>
<tr>
<td>on-visible-change</td>
<td>菜单显示状态改变时调用</td>
<td>visible</td>
</tr>
<tr>
<td>on-clickoutside</td>
<td>点击外部关闭下拉菜单时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-dropdownslot1_3" class="vue-doc-element">Dropdown slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>主体内容</td>
</tr>
<tr>
<td>list</td>
<td>列表内容，一般由 DropdownMenu 承担</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-dropdownitemprops1_4" class="vue-doc-element">DropdownItem props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>用来标识这一项</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用该项</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>divided</td>
<td>显示分割线</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>selected</td>
<td>标记该项为选中状态</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demotrigger from './demo/trigger.md';
import demoplacement from './demo/placement.md';
import demonest from './demo/nest.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demotrigger,demoplacement,demonest
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


